.logo {
    height: 75px;
    width: 320px;
}

.logo_text {
    font-family: 'Noto Sans KR';
    font-style: normal;
    font-weight: 900;
    font-size: 33px;
    line-height: 24px;
    /* identical to box height, or 73% */

    display: flex;
    align-items: flex-end;

    color: #FFFFFF;
}

.input {
    width: 510px;
}

.input_text {
    font-family: 'Noto Sans KR';
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 35px;
    margin-bottom: 4px;
    /* identical to box height */

    display: flex;
    color: #FFFFFF;
}

.login_text {
    font-family: 'Noto Sans KR';
    font-style: normal;
    font-weight: 700;
    font-size: 22px;
    line-height: 32px;
    display: flex;
    align-items: center;
    text-align: center;
}

.button {
    margin-top: 50px;
}